.account-nav {
  @include list-reset();
}

@media (min-width: $desktop-width) {
  .account-nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: $background-color;
    border: 1px solid $background-color;
    border-radius: 4px;
    position: relative;
  }
}
